<template>
<div>
    <header>
        <p>商品</p>
        <p>评价</p>
        <button @click="go">返回</button>
    </header>
    <div>
        <van-tree-select height="55vw" :items="items" :main-active-index.sync="active">
  <template #content>
    <van-image
      v-if="active === 0"
      src="https://img.yzcdn.cn/vant/apple-1.jpg"
    />
    <van-image
      v-if="active === 1"
      src="https://img.yzcdn.cn/vant/apple-2.jpg"
    />
  </template>
</van-tree-select>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
        active: 0,
      items: [{ text: '热销' }, { text: '折扣' }],
   }
  },
  mounted(){
      this.ajaxGet()
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   
    async ajaxGet(){
        let { data } = await axios.get('http://elm.cangdu.org/shopping/v2/menu?restaurant_id=1')
        // console.log(data[0].name)
        // this.items = data.name
    },
    go(){
        this.$router.back()
    }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
header{
    height: 50px;
    background-color: #eee;
    display: flex;
    justify-content: space-around;
}
</style>
