<template>
<div class="big" :class="flag?'boll':''">
    <div class="small" @click="dian()" :class="flag?'bol':''"></div>
</div>
</template>

<script>
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props:["flag"],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {}
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
      dian(){
          this.$emit('dian')
      }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped lang="scss">
    .big{
        width: 50px;
        height: 20px;
        background-color: #aaa;
        border-radius: 15px;
        position: relative;

    }
    .big .bol{
        left: 30px;      
    }
    .boll{
        background-color: blue;
        transition: all 1s;
    }
    .small{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: red;
        position: absolute;
        left: 0;
        transition: all 1s;

    }
   
</style>
