<template>
<div>
  <van-nav-bar
  :title="this.$route.query.name"
  left-text="返回"
  right-text="切换城市"
  left-arrow
  @click-left="onClickLeft"
  @click-right="onClickRight"
/>
<div>
<van-search
  v-model="value"
  show-action
  label="地址"
  placeholder="请输入搜索关键词"
  @search="onSearch"
>
  <template #action>
    <div @click="onSearch">搜索</div>
  </template>
</van-search>
</div>
<div v-show="flag">
  <h5>历史记录</h5>
  <button @click="clears()">清空历史记录</button>
  <ul class="lishi" v-show="flag"> 
    <li v-for="(item,index) in seached" :key="index">
          <h4>{{ item.name }}</h4>
          <p>{{ item.address }}</p>
    </li>
  </ul>
</div>

<ul class="list">
  <li v-for="(item,index) in list" :key="index" @click="go(item)" v-show="!flag">
    <h4>{{ item.name }}</h4>
    <p>{{ item.address }}</p>
  </li>
</ul>
</div>

</template>

<script>
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
        value: '',
        list:[],
        seached:JSON.parse(localStorage.getItem("seached"))||[], 
        flag:true
   }
  },
  // 计算属性
  computed: {},
  mounted(){
  
     
    
  },
  // 侦听器
  watch: {
      value(val){
          if(val==''){
            this.flag = true
          }
      },
      seached:{
        handler(val){
          localStorage.setItem('seached',JSON.stringify(val))
        },
        deep:true
      }

  },
  
  // 组件方法
  methods: {
      onClickLeft() {
      this.$router.back()
    },
    onClickRight() {
           this.$router.back()
    },
    onSearch() {
      //  if(this.value==''){
      //   this.flag=true
      // }else{
      //   this.flag=false
      // }
      // console.log(this.flag)
      this.flag =false
      console.log(this.flag)
      if(this.value==''){
        alert('请填写地址')
        return false
      }else{
               this.$http.get(`/v1/pois?type=search&city_id=${this.$route.query.id}&keyword=${this.value}`).then((data)=>{
                    console.log(data)
                    this.list = data.data               
                  })                
      }
    },
    go(item){
      // let seached = localStorage.getItem('seached')||[]
      this.seached.unshift(item)
      if(this.seached.length>5){
            this.seached.pop(item)

      }
      localStorage.setItem('seached',JSON.stringify(this.seached))
      console.log(this.seached.length)
    // console.log(this.seached)
      this.$router.push('/shopList')

    },
    clears(){
        this.seached=[]
    }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped lang="scss">
ul,li{
  list-style: none;
  padding: 5px 0;
}
li{
  border: 1px solid red;

}
h4,p{
  margin: 0;
  padding: 0;
}
</style>
