<template>
<div>
    <app-header>
      <div slot="left" @click="seach()">
       <van-icon name="search" size="26"/>
      </div>
      <div slot="title"></div>
      <div slot="right" @click="user()"><van-icon name="manager-o" size="26"/></div>
    </app-header>
    <div class="banner">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <!-- <van-swipe-item></van-swipe-item> -->
                  <van-swipe-item>
                      <ul class="jiu">
                          <li v-for="(item,index) in arrImg" :key="index" v-if="index<8">
                              <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                             <p>{{item.title}}</p> 
                          </li>
                      </ul>
                </van-swipe-item>
          
                <van-swipe-item>
                      <ul class="jiu">
                          <li v-for="(item,index) in arrImg" :key="index" v-if="index>7">
                            <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                             <p>{{item.title}}</p> 
                          </li>
                      </ul>
                </van-swipe-item>
        </van-swipe>     
    </div>
    <div>
        <h3>附近商家</h3>
         <van-card
         v-for="(item,index) in list" :key="index"
         @click="shop(item.id)"
         :desc="'月销'+item.recent_order_num"

         
        :price="item.float_minimum_order_amount+'起送'+'/'+item.piecewise_agent_fee.tips"
        :title="item.name"
        :thumb="'//elm.cangdu.org/img/'+item.image_path"
        >
  <template #tags>
    <van-tag plain type="danger">蜂鸟转送</van-tag>
    <van-tag plain type="danger">准时达</van-tag>
  </template>
  <template #footer>

        <van-tag plain type="danger">{{ item.distance }}</van-tag>
    <van-tag plain type="danger">{{ item.order_lead_time }}</van-tag>
  </template>
</van-card>
    </div>
   
    <router-view></router-view>
</div>
</template>

<script>
import axios from 'axios'
import appHeader from '../components/header'
export default {
 // 组件名称
 name: '',
 // 组件参数 接收来自父组件的数据
 props: [],
 // 局部注册的组件
 components: {
     appHeader
 },
 // 组件状态值
 data () {
 return {
     arrImg:[],
     list:[]
 }
 },
 // 计算属性
 computed: {},
 // 侦听器
 watch: {},
 // 组件方法
 methods: {
     async ajaxImg(){
         let {data} = await axios.get('http://elm.cangdu.org/v2/index_entry?geohash=75.187877,13.548463&group_type=1&flags[]=F')
        // console.log(data)
        this.arrImg = data
        let {data:res} = await axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=45.788607&longitude=126.651464&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=')
        console.log(res)
        this.list = res
      },
     seach(){
         this.$router.push('/city')
     },
    user(){
         this.$router.push('/user')
     },
     shop(id){
         this.$router.push('/shopList')

     }

 },
 /**
 * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
 */
 created () {
 },
 /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
 mounted () {
this.ajaxImg()
 },
 /**
 * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
 * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
 */
 updated () {
 },
 /**
 * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
 */
 activated () {
 },
 /**
 * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
 */
 deactivated () {
 },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped lang="scss">
 .my-swipe .van-swipe-item {
    color: blueviolet;
    font-size: 14px;
    // line-height: 150px;
    text-align: center;
    background-color: #fff;
  }
img{
    width: 40px;
    height: 40px;
}
.jiu{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    li{
        width: 25%;
        // height: 50px;
    }
}
</style>
