<template>
<div class="box">
        <header>
        <span @click="fan()">返回</span>
        <span>密码登录</span> 
        <span></span>
    </header>
    <div>
        <p class="in"><input type="text" placeholder="账号" v-model.trim="username"></p>     
       <p class="in"> 
           <input placeholder="密码" :type="!flag?'password':'text'" maxlength="6" v-model.trim="password">
            <qiu @dian="dian" :flag="flag"></qiu>    
       </p>
       <div class="in"> 
           <input type="text" placeholder="验证码" maxlength="4" v-model.trim="verify">
              <p>
                  <img :src="imgUrl" alt="">
                  <span @click="getVerify">看不清换一张</span>
              </p> 
       </div>
    
    <p class="siz">温馨提示：未注册过的账号，登录时将自动注册</p>
    <p class="siz"> 注册过的用户可凭账号密码登录</p>
    <footer>
        <van-button type="primary" :disabled="!isDisabled" block @click="submit()">登录</van-button>
        <p>重置密码</p>
    </footer>
    </div>
</div>


</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials = true; //配置为true
import qiu from '@/components/qiu.vue'
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {
      qiu
  },
  created(){
    this.getVerify()
  },
  // 组件状态值
  data () {
   return {
       imgUrl:'',
       flag:false,
       username:"",
       password:"",
       verify:"",
    //    isDisabled:false
   }
  },
  // 计算属性
  computed: {
        isDisabled(){
            return this.username !="" && this.password !="" && this.verify !=""
        }
  },
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
        setCookie(key, value, t) {
            var oDate = new Date();
            oDate.setDate(oDate.getDate() + t);
            document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
        },
      fan(){
          this.$router.back()
      },
      getVerify(){
          axios.post('http://elm.cangdu.org/v1/captchas').then(({data})=>{
              this.imgUrl = data.code
            //   console.log(this.imgUrl)
          })
      },
      submit(){
          this.setCookie("cap", this.verify, 2);
          this.setCookie("SID", 100, 2);
          let reg = /^1(3[0-9]|5[0-3,5-9]|7[1-3,5-8]|8[0-9])\d{8}$/;
          if(reg.test(this.username) && this.password != "" && this.verify != ""){

              axios.post("/api/v2/login",{
                  username:this.username,
                  password:this.password,
                  captcha_code:this.verify
              })
              .then(res => {
                  console.log(res);
                  if(res.status==200){
                    //   sessionStorage
                      sessionStorage.setItem("token",JSON.stringify(res.data.id))
                      this.$toast.success('登录成功');
                        this.$router.push({
                        name:"list",
                        // params:{age:res.data}
                        })
                  }
              })
          }
      },

      dian(){
        this.flag = !this.flag
      }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped lang="scss">
*{
    margin: 0;
    padding: 0;
}
html,body{
    height: 100%;
}
.box{
    width: 100%;
    display: flex;
    flex-direction: column;
}
header{
 background-color: violet;
 line-height: 50px;
 display: flex;
 justify-content: space-around;
}
.in{
    // width: 100%;
    line-height: 50px;
    display: flex;
    justify-content: space-between;
    input{
        outline: none;
        border: 0;
        height: 50px;
    }
    img{
        width: 100px;
        height: 50px;
    }
    span{
        line-height: 40px;
    }
}
.siz{
    font-size: 14px;
    color: tomato;
    padding-left: 20px;
}
footer{
    text-align: center;
    button{
    width: 90%;
    background-color: tomato;
    color: #fff;
    text-align: center;
    line-height: 30px;
}
}

</style>
