<script>
import axios from 'axios'
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      active: 3,
      name:'',
      userId:sessionStorage.getItem('token')
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  mounted(){
      console.log(this.userId)
      this.getAxios()
  },
  watch: {},
  // 组件方法
  methods: {
    go() {
      this.$router.push("/");
    },
    async getAxios(){
        let {data} = await axios.get(`http://elm.cangdu.org/v1/user?user_id=${this.userId}`)
        console.log(data.username)
        this.name = data.username
    }
  },
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
